/*  */
import './index.less';
import React from 'react';
import { Row, Col, Form, Switch, Radio, InputNumber, Button, Modal, message } from 'antd';

const { confirm } = Modal;

class swpierConf extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let swiperConf = null;
        swiperConf = {...values,
          observer: true
        };
        if (swiperConf.pagination) {
          swiperConf = {...swiperConf,
            pagination: {
              clickable :true,
              el: '.swiper-pagination'
            }
          };
        }
        if (swiperConf.navigation) {
          swiperConf = {...swiperConf,
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev'
            }
          };
        }
        if (swiperConf.itemcount > 20) {
          confirm({
            title: '',
            content: '',
            onOk() {
              this.props.callbackSubmit(swiperConf);
            },
            onCancel() {
              message.success('');
            }
          });
        } else {
          this.props.callbackSubmit(swiperConf);
        }
      }
    });
  };
  handleFormresetFields = () => {
    this.props.form.resetFields();
    let swiperConf = {
      loop: true,
      itemcount: 3,
      autoplay: true,
      observer : true,
      pagination: {
        clickable :true,
        el: '.swiper-pagination'
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      }
    };
    this.props.callbackReset(swiperConf);
  };
  disabledRadio = () => {
    let direction = this.props.form.getFieldsValue()['direction'];
    let effect = this.props.form.getFieldsValue()['effect'];
    if(direction==='horizontal' && effect!=='slide' && effect!=='fade') {
      this.props.form.setFieldsValue({
        effect: 'slide'
      });
    }
  };
  constructor(props) {
    super(props);
    this.state = {
      swiperTpl: null,
      swiperConf: this.props.conf,
      formItemLayout: {
        min: {
          labelCol: { span: 5 },
          wrapperCol: { span: 19 }
        },
        mid: {
          labelCol: { span: 7 },
          wrapperCol: { span: 17 }
        },
        max: {
          labelCol: { span: 8 },
          wrapperCol: { span: 16 }
        }
      }
    };
  };
  render() {
    const { getFieldDecorator } = this.props.form,
    { formItemLayout } = this.state,
    disabled = this.props.form.getFieldsValue()['direction']==='vertical';
    return (
      <Row className="swpierConf">
        <Col span={ 12 }>
          <Form layout="horizontal" onSubmit={this.handleSubmit}>
            <div className="formInner">
              <Form.Item label="" {...formItemLayout.mid}>
                {
                  getFieldDecorator('itemcount', {
                    initialValue: 3,
                    rules: [{
                      required: true,
                      message: '',
                      type: 'number'
                    }, {
                      pattern: /^[1-9]\d*$/,
                      message: '0'
                    }]
                  })(
                    <InputNumber min={1} />
                  )
                }
              </Form.Item>
              <Form.Item label="" {...formItemLayout.min}>
                {
                  getFieldDecorator('autoplay', {
                    valuePropName: 'checked',
                    initialValue: true
                  })(
                    <Switch />
                  )
                }
              </Form.Item>
              <Form.Item label="" {...formItemLayout.min}>
                {
                  getFieldDecorator('loop', {
                    valuePropName: 'checked',
                    initialValue: true
                  })(
                    <Switch />
                  )
                }
              </Form.Item>
              <Form.Item label="" {...formItemLayout.max}>
                {
                  getFieldDecorator('pagination', {
                    valuePropName: 'checked',
                    initialValue: true
                  })(
                    <Switch />
                  )
                }
              </Form.Item>
              <Form.Item label="" {...formItemLayout.max}>
                {
                  getFieldDecorator('navigation', {
                    valuePropName: 'checked',
                    initialValue: true
                  })(
                    <Switch />
                  )
                }
              </Form.Item>
              <Form.Item label="" {...formItemLayout.min}>
                {
                  getFieldDecorator('direction', {initialValue: 'horizontal'})(
                    <Radio.Group onChange={this.disabledRadio}>
                      <Radio value="horizontal"></Radio>
                      <Radio value="vertical"></Radio>
                    </Radio.Group>
                  )
                }
              </Form.Item>
              <Form.Item label="" {...formItemLayout.min}>
                {
                  getFieldDecorator('effect', {initialValue: 'slide'})(
                    <Radio.Group>
                      <Radio value="slide"></Radio>
                      <Radio value="fade"></Radio>
                      <Radio value="cube" disabled={disabled}></Radio>
                      <Radio value="coverflow" disabled={disabled}>3D</Radio>
                      <Radio value="flip" disabled={disabled}>3D</Radio>
                    </Radio.Group>
                  )
                }
              </Form.Item>
            </div>
            <Form.Item wrapperCol={{ span: 24, offset: 16 }}>
              <Button type="primary" htmlType="submit">
                
              </Button>
              &nbsp;
              <Button type="default" onClick={ this.handleFormresetFields }>
                
              </Button>
            </Form.Item>
          </Form>
        </Col>
      </Row>
    );
  }
}

export default Form.create({ name: 'swpierConf' })(swpierConf);
